import React, { Component } from 'react';
// import PropTypes from 'prop-types';

import WDs from './WDs';
import Control from './Control';
import Month from './Month';
import Slide from '../Transition/Slide';

import format from 'date-fns/format';
import isSameDay from 'date-fns/is_same_day';
import isValid from 'date-fns/is_valid';
import addMonths from 'date-fns/add_months'

import './scss/calendar.css';
import './scss/calendar-effects.css';

class Calendar extends Component {
	static CLASSES_WRAPPER = 'rmd-cal--wrapper';

	constructor(props) {
		super(props);

		const today = new Date();

		this.state = {
			today: today,
			activeDay: today,
			month: new Date(today.getFullYear(), today.getMonth()),
			prevMonth: null,
		};

		this.handleDayClick = this.handleDayClick.bind(this);
		this.handleNextMonthClick = this.handleNextMonthClick.bind(this);
		this.handlePrevMonthClick = this.handlePrevMonthClick.bind(this);
	}

	handleDayClick(e) {

		const dayName = e.target.dataset.value;

		if (! dayName) {
			return;
		}

		const [ yearStr, monthStr, dayStr ] = dayName.split('-');

		const day = new Date(yearStr, monthStr - 1, dayStr);

		if (! isValid(day)) {
			return;
		}

		if (isSameDay(day, this.state.activeDay)) {
			return;
		}

		this.setState({ activeDay: day });
	}

	handlePrevMonthClick(e) {
		this.setState({
			month: addMonths(this.state.month, -1),
			prevMonth: this.state.month,
		});
	}

	handleNextMonthClick(e) {
		this.setState({
			month: addMonths(this.state.month, 1),
			prevMonth: this.state.month,
		});
	}

	render() {
		return (
			<div className={ this.constructor.CLASSES_WRAPPER }>
				<Control
					month={ this.state.month }
					onPrevMonth={ this.handlePrevMonthClick }
					onNextMonth={ this.handleNextMonthClick }
				/>
				<WDs/>
				<Slide
					activeKey={ format(this.state.month, 'YYYY-MM') }
					animation={ this.state.month > this.state.prevMonth ? 'right-to-left' : 'left-to-right' }
				>
					<Month
						data-slot="active"
						month={ this.state.month }
						today={ this.state.today }
						activeDay={ this.state.activeDay }
						onDayClick={ this.handleDayClick }
					/>
					{
						this.state.prevMonth ?
						<Month
							data-slot="prev"
							month={ this.state.prevMonth }
							today={ this.state.today }
							activeDay={ this.state.activeDay }
							onDayClick={ this.handleDayClick }
						/>
						: null
					}
				</Slide>
			</div>
		);
	}
}

export default Calendar;



